body {
  color: #2f2f37;
  background-color: #fff;
  font-family: monospace;
  line-height: 1.5;
}

a:link, a:visited {
  color: #fff;
  text-decoration: none;
}

.poke-container {
  padding-left: 15px;
  padding-right: 15px;
}

.poke_title {
  color: #fff;
  justify-content: center;
  display: flex;
}

.poke-nav {
  background-color: #4b4747;
  justify-content: space-between;
  align-items: center;
  padding: 15px 25px;
  display: flex;
  box-shadow: 0 2px 10px #0003;
}

.favorites-link {
  color: #fc0;
  border-radius: 8px;
  align-items: center;
  gap: 5px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: bold;
  transition: all .25s;
  display: flex;
}

.favorites-link:hover {
  background-color: #ffffff26;
  transform: translateY(-1px);
}

.favorites-count {
  color: #fff;
  background-color: #ff5722;
  border-radius: 12px;
  padding: 2px 8px;
  font-size: 12px;
}

.poke-form_box {
  color: #fff;
  text-align: center;
  background-color: #4b4747;
  flex-direction: column;
  padding: 30px 15px 20px;
  display: flex;
}

.poke_form input {
  border: none;
  border-radius: 10px;
  width: 600px;
  max-width: 90%;
  height: 32px;
  margin-bottom: 8px;
  padding-left: 10px;
}

.poke_form input:focus {
  outline: 2px solid #fc0;
}

.poke-filt_sort {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.poke_sort, .poke_filter {
  color: #4b4747;
  background-color: #fff;
  border: 1px solid #4b4747;
  border-radius: 5px;
  padding: 5px 10px;
  transition: box-shadow .2s;
}

.poke_sort:hover, .poke_filter:hover {
  box-shadow: 0 0 6px #00000026;
}

.range-control {
  flex-direction: column;
  margin-right: 15px;
  display: flex;
}

.poke_weights_range, .poke_heights_range {
  width: 150px;
}

.pokemon-type {
  color: #333;
  background-color: #eee;
  border-radius: 12px;
  margin-right: 5px;
  padding: 4px 8px;
  font-size: 12px;
  display: inline-block;
}

.card-container, .favorites-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 30px;
  display: flex;
}

.card, .favorite-card {
  color: #4b4747;
  background: #fafafa;
  border-radius: 16px;
  flex-direction: column;
  width: 180px;
  transition: all .25s;
  display: flex;
  box-shadow: 0 2px 6px #0000001a;
}

.card:hover, .favorite-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px #0003;
}

.card-img-top, .favorite-card .card-img-top {
  background-color: #d5d5d9;
  border-radius: 16px 16px 0 0;
  justify-content: center;
  align-items: center;
  height: 150px;
  display: flex;
}

.card-img-top-card {
  width: 80%;
}

.card-body, .favorite-card .card-body {
  flex-direction: column;
  flex-grow: 1;
  gap: 7px;
  padding: 10px;
  display: flex;
}

.add-to-favorites-btn, .remove-from-favorites-btn {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  width: 100%;
  margin-top: auto;
  padding: 8px 12px;
  font-size: 14px;
  transition: all .25s;
}

.more_btn {
  color: #fff;
  cursor: pointer;
  background-color: #4b4747;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .25s;
  display: block;
}

.more_btn:hover {
  background-color: #715353;
  transform: translateY(-2px);
}

.add-to-favorites-btn {
  color: #fff;
  background-color: #4caf50;
}

.add-to-favorites-btn:hover {
  background-color: #43a047;
}

.add-to-favorites-btn.favorited {
  color: #333;
  background-color: #fc0;
}

.remove-from-favorites-btn {
  color: #fff;
  background-color: #e53935;
}

.remove-from-favorites-btn:hover {
  background-color: #d32f2f;
}

.favorites-section {
  flex-direction: column;
  align-items: center;
  min-height: 300px;
  padding: 20px;
  display: flex;
}

.favorites-title {
  color: #4b4747;
  margin-top: 10px;
  margin-bottom: 15px;
  font-size: 22px;
  font-weight: bold;
}

.back-to-search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4b4747;
  border: none;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 8px 16px;
  transition: all .25s;
}

.back-to-search-btn:hover {
  background-color: #333;
}

.empty-state {
  text-align: center;
  color: #888;
  padding: 40px;
  font-style: italic;
}
